<template lang="pug">
    .left-tab-w(:class="{'horizontalitemw':horizontal}")
        .tab-item(v-for="(item,index) in tabList" :key="item.key" :class="{'tab-item-active':item.key===tab && !horizontal,'horizontalcitem':item.key===tab && horizontal}" @mouseenter="handleEnter(item)") {{item.name}}
</template>

<script>
export default {
  data() {
    return {
      tab: ""
    };
  },
  props: {
    tabList: Array,
    horizontal: {
      type: Boolean,
      default: false
    }
  },
  created() {
    this.tabList.forEach(item => {
      if (item.act) {
        this.tab = item["key"];
      }
    });
  },
  methods: {
    handleEnter(obj) {
      this.tab = obj["key"];
      this.$emit("onTab", obj);
    }
  }
};
</script>

<style lang="less" scoped>
.left-tab-w {
  width: 100%;
  .tab-item {
    font-size: 16px;
    color: #666;
    height: 58px;
    line-height: 58px;
    text-align: center;
    border-bottom: 1px solid #fff;
    transition: all linear 0.3s;
    background: #ececec;
  }
  .tab-item-active {
    background: #ff6637;
    color: #fff;
    height: 80px;
    line-height: 80px;
  }
  .horizontalcitem {
    background: #ff6637;
    color: #fff;
    text-align: center;
  }
}
.horizontalitemw {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
  .tab-item{
      flex: 1;
  }
}
</style>