<template lang="pug">
    .himg-box
        <el-image :src="scr" :fit="fit"></el-image>
        .footer-bar {{text}}
</template>

<script>
export default {
  data() {
    return {
      fit: "cover"
    };
  },
  props: {
    scr: String,
    text: String
  }
};
</script>

<style lang="less" scoped>
.himg-box {
  position: relative;
  /deep/ img{
      transition: all linear .5s;
      &:hover{
          transform: scale(1.3);
      }
  }
  .footer-bar {
    position: absolute;
    left: 0;
    bottom: 3px;
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    background: rgba(0, 0, 0, 0.5);
    font-size: 13px;
    overflow: hidden;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
</style>